@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

:root {
  --one: #ffcb74;
  --two: #111;
  --three: #f6f6f6;
  --four: #2f2f2f;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  color: var(--three);
  background-color: var(--two);
  font-size: 20px;
}

h1, h2, h2, h3 {
  font-weight: 500;
  margin: 0;
}

h1 {
  font-size: 3em;
  margin-left: -2.5px;
  color: var(--one);
}

h2 {
  font-size: 1em;
}

h3 {
  font-size: 0.7em;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.home, .skill, .project, .contact {
  display: grid;
  padding: 15px;
  gap: 1.5em;
  margin-bottom: 1em;
}

.home, .contact {
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
}

.home {
  min-height: 100vh;
  box-sizing: border-box;
}

.home .box h3 {
  margin-bottom: 15px;
}

.home .box .other {
  display: flex;
  align-items: center;
}

.home .box .other .icon {
  color: var(--one);
  margin-right: 10px;
}

.home .profile {
  position: relative;
}

.home .profile img {
  width: 100%;
  display: block;
}

.home .profile .frame {
  position: absolute;
  border: 3px solid var(--one);
  top: 50px;
  left: 50px;
  bottom: 50px;
  right: 50px;
  transform: rotate(30deg);
  z-index: -99;
  animation: icon 1s;
}

.skill {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(10, 1fr);
  gap: 1em;
}

.skill img{
  width: 100%;
}

.list {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5em;
}

.project a {
  color: var(--one);
}

.image {
  position: relative;
  height: 250px;
  background-color: var(--four);
}

.image img {
  filter: brightness(50%);
  width: 100%;
  height: 100%;
}

.image .text {
  padding: 10px 15px;
  position: absolute;
  bottom: 0;
}

.image .text h2 {
  margin-bottom: 2px;
}

.image .text .visit {
  margin-top: 13px;
  width: max-content;
}

.image .text a {
  text-decoration: none;
}

.social {
  display: flex;
  gap: 0.5em;
  margin-top: 15px;
}

.social a {
  font-size: 1.5em;
  background-color: var(--four);
  color: var(--one);
  line-height: 100%;
  padding: 7px 9px 5px;
}

form {
  display: grid;
}

form input, form textarea {
  outline: none;
  background-color: transparent;
  border: 1px solid var(--four);
  padding: 10px;
  font-size: 0.7em;
  font-family: 'Poppins', sans-serif;
  color: var(--three);
  resize: none;
  margin: 5px 0 15px;
}

form button {
  padding: 10px;
  background-color: var(--four);
  outline: none;
  cursor: pointer;
  font-size: 1em;
  color: var(--one);
  border: none;
  font-family: 'Poppins', sans-serif;
}

.footer {
  padding: 15px;
}

.dis {
  filter: brightness(75%);
}

.alert {
  position: fixed;
  top: 5px;
  right: 5px;
  padding: 0 10px;
  background-color: var(--four);
  color: var(--one);
  z-index: 99;
}

::selection {
  background-color: var(--four);
  color: var(--one);
}

@media screen and (max-width: 750px) {
  body {
    font-size: 16px;
  }

  .home, .list, .contact {
    grid-template-columns: repeat(1, 1fr);
  }

  .skill {
    grid-template-columns: repeat(5, 1fr);
  }
}

@keyframes icon {
  0% {transform: rotate(0deg)}
  30% {transform: rotate(30deg)}
  70% {transform: rotate(25deg)}
  80% {transform: rotate(30deg)}
  95% {transform: rotate(28deg)}
  100% {transform: rotate(30deg)}
}
